import React from "react";
import { Scrollbars } from "react-custom-scrollbars";
import { Grid } from "@material-ui/core";
import { PlatformSummary } from "./components/cards/PlatformSummary";
import { useStyles } from "../start-page/components/StyleClasses";

export default function StartPage() {
  const classes = useStyles();
  const elevation = 0;

  return (
    <Scrollbars autoHide hideTracksWhenNotNeeded style={{ width: '100%' }} autoHeightMin="100%" autoHeightMax="100%">
      <Grid container spacing={2} className={classes.root}>
        <Grid item xs={3}>
          Quick links
        </Grid>
        <Grid item xs={9}>
          <PlatformSummary elevation={elevation}/>
        </Grid>
      </Grid>
    </Scrollbars>
  );
}
